.quiz {
	width: 500px;
	border-radius: 30px;
	padding: 40px;
	background-color: #fff;
	position: relative;
	&:before,
	&:after {
		content: "";
		display: block;
		height: 50px;
		border-bottom-left-radius: 30px;
		border-bottom-right-radius: 30px;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
	}

	&:before {
		width: 95%;
		bottom: -10px;
		background-color: rgba(255, 255, 255, 0.8);
		z-index: 0;
	}

	&:after {
		width: 90%;
		bottom: -20px;
		background-color: rgba(255, 255, 255, 0.3);
		z-index: 1;
	}

	h1 {
		margin: 0;
	}
}
